</template>

<script>
  export default {
    name: 'TransThree'
  }
</script>

<style scoped>
  *{
    box-sizing: border-box;
  }
  .trans{
    display: flex;
    align-items: center;
    justify-content: center;
    /* height: 100vh; */
  }
  .transimg{
    width: 20px;
    height: 20px;
    transform-style: preserve-3d;
    animation: animas 5s ease-in-out infinite;
  }
  .transimg > div{
    width: 20px;
    height:20px;
    text-align: center;
    line-height: 20px;
    opacity: 0.8;
