<template>
  <swiper class="w">
    <swiper-item v-for="(item, index) of banners" :key="index">
      <img :src="item.iimagesrc" @load="sILoad">
    </swiper-item>
  </swiper>
</template>

<script>
  import {Swiper, SwiperItem} from 'common/swiper/index'

  export default {
    name: 'HomeSwiper',
    components: {
      Swiper,
      SwiperItem
    },
    props: {
      //轮播图的信息
      banners: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data() {
      return {
        //判断图片是否加载完成
        isLoad: false
      }
    },
    methods: {
      //监听图片是否加载完成
     sILoad() { 
        //判断图片是否加载完成
        if(!this.isLoad) {
          //发送事件
          this.$emit('sILoad');
          this.isLoad = true
        }

      }
    }
  }
</script>

<style scoped>
  .w{
    height: 220px;
  }
  img{
    height: 220px;
  }
</style>