<template>
  <div class="tab-control">
    <div class="tab-control-item" v-for="(item, index) in titles" :key="item" :class="{cactive: index === currentIndex}" @click="itemChange(index)">
      <span>{{item}}</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'TanControl',
    props: {
      //展示标题的内容
      titles:{ 
        type: Array,
        default() {
          return []
        }
      }
    },
    data() {
      return {
        currentIndex:0//用来监听修改标题的数据
      }
    },
    methods: {
      //点击修改标题样式
      itemChange(index) {
        //修改对应的下标改变样式
        this.currentIndex = index;
        //传递数据给父组件
        this.$emit('tabClick', index);
      }
    }
  }
</script>

<style scoped>
  .tab-control{
    display: flex;
    text-align: center;
     height: 40px;
    line-height: 40px;
    font-size: 14px;
    background-color: white;
  }
  .tab-control-item{
    flex: 1;
  }
  .tab-control-item span{
    padding-left: 5px;
     padding-right: 5px;
     padding-bottom: 3px;
  }
  .cactive{
    color: #ff8198;
  }
  .cactive span{
    border-bottom: 3px solid #ff8198;
     
  }
</style>