<template>
  <div>
    <div class="retext">每日推荐</div>

    <div class="recommend"> 
      
      <div class="recommend-item" v-for="(item, index) in recommend" :key="index" @click="goodsClick(item.gid)"> 
          <img :src="item.gimage" alt="">
          <div>{{item.gtitle}}</div> 
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'HomeRecommend',
    props: {
      //接受每日推荐的数据
      recommend: {
        type: Array,
        default() {
          return [] 
        }
      }
    },
    methods: {
      goodsClick(gid) {
        this.$router.push('/detail/' + gid)
      }
    }
  }
</script>

<style scoped>
  .retext{
    text-align: center;
    padding-top: 8px;
    font-size: 18px;
  }
  .recommend{
    display: flex;
    width: 100%;
    text-align: center;
    font-size: 14px;
    padding: 10px 0 20px;
    border-bottom: 10px solid #eee;
   
  }
  .recommend-item{
    flex: 1;
  }
  .recommend img{
    width: 65px;
    height: 65px;
    padding-bottom: 8px;
  }
</style>