<template>
  <div id="home">
    <nav-bar class="nav-style">
      <div slot="left" class="nav-left">
        <trans-text></trans-text>
      </div>
      <div slot="center">佳佳蛋糕</div>
      <div slot="right" class="nav-right">
        <trans-text></trans-text>
      </div>
    </nav-bar>
    <tab-control :titles="['流行', '新品', '精选']" class="tabcon" v-show="isTabControl" ref="tabControl1" @tabClick="tabClick"></tab-control>

    <scroll class="content" ref="scroll" @scroll="cScroll" :probe-type="3" @pullingUp="loadMore" :pull-up-load="true"> 
      <home-swiper @sILoad="sILoad" :banners="banners"></home-swiper>
      <!-- <trans-three></trans-three> -->
      <!-- <trans-text></trans-text> -->
      <home-recommend :recommend="recommend"></home-recommend>

      <tab-control :titles="['流行', '新品', '精选']" @tabClick="tabClick" ref="tabControl2"></tab-control>
      <!--展示首页的商品-->
      <goods-list :goods="showGoods"></goods-list>
    </scroll>

    <!--置顶按钮   监听组件根元素点击要用native-->
    <back-top @click.native="backClick" v-show="isShowBackTop"></back-top>
  </div>
</template>

<script>
import NavBar from  'common/navbar/NavBar'
import TransThree from 'common/transthree/TransThree'
import TransText from 'common/transthree/TransText'
import Scroll from 'common/scroll/Scroll'


import TabControl from 'content/tabcontrol/TabControl'
import GoodsList from 'content/goods/GoodsList'

import HomeSwiper from './childcomps/HomeSwiper'
import HomeRecommend from './childcomps/HomeRecommend'

import {itemImageMixin, backTopMixin} from 'commons/mixin'
import {getHomeMultidata, getHomeGoods} from 'network/home'

  export default {
    name: 'Home',
    components: {
      NavBar,
      TabControl,
      HomeSwiper,
      HomeRecommend,
      TransThree,
      TransText,
      GoodsList,
      Scroll
    },
    data() {
      return {
        //首页轮播图的数据
        banners: [],
        //传到每日推荐组件的数据
        recommend:[
          {
            link: '',
            image: require('assets/img/111.png'),
            title: '卡通'
          },
          {
            link: '',
            image: require('assets/img/222.png'),
            title: '生日'
          },
          {
            link: '',
           image: require('assets/img/333.png'),
            title: '寿糕'
          },
          {
            link: '',
           image: require('assets/img/333.png'),
            title: '寿糕'
          }
        ],
        //首页商品展示的信息
        goods: {
          'pop': {page: 0, list: []},
          'news': {page: 0, list: []},
          'sell': {page: 0, list: []}
        },
        //用来接受子组件点击传递过来的数据类型
        currentType: 'pop',
        //用来保存tabcontrol的offsettop的值
        tabOffsetTop: 0,
        //用来判断第一个tabcontrol是否显示
        isTabControl: false,
        //用来保存界面离开时的位置
        saveY: 0
      }
    },
    created () {
      //请求多个数据
      getHomeMultidata().then(res => {
        this.banners = res[0];
        this.recommend = res[1];
      })
      //请求首页的商品数据
      this.getHomeGoods('pop')
      this.getHomeGoods('sell')
      this.getHomeGoods('news')
    },
    //混入
    mixins: [itemImageMixin, backTopMixin],
    //页面活跃时调用
    activated () {
      //跳到离开时的位置
      this.$refs.scroll.scrollTo(0, this.saveY, 0);
      //刷新
      this.$refs.scroll.refresh()
    },
    mounted () {
      //接受改变商品收藏数量
      this.$bus.$on('changeCounts', (gid,gcollect,gkind) => {
        var goodsKind = ['pop','sell','news'];
        //根据gid查找商品修改收藏数量
        for(var i = 0;i<goodsKind.length;i++) {
          let names = goodsKind[i];
          //判断种类
          if(names == gkind) {
          let g =this.goods[names].list;
          for(var j = 0;j < g.length;j++) {
            if(g[j].gid == gid) {              
              g[j].gcollect = gcollect;
            }
          }
          }
         
        }
        
      })
    },
    //页面离开时调用
    deactivated () {
      //保存离开时的位置
      this.saveY = this.$refs.scroll.getScrollY();
      //取消全局事件的监听
      this.$bus.$off('itemImageLoad', this.itemListener)
    },
    computed: {
      //点击分类
      showGoods() {
        return this.goods[this.currentType].list
      }
    },
    methods: {
      //当轮播图加载完成时获取获取tabcontroler的offsettop  
      sILoad() {
        // 所有的组件对象有一个属性$el 获取元素对象
        this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop
      },
      //事件监听
      tabClick(index) {
        switch (index) {
          case 0: this.currentType = 'pop';
                  break;
          case 1: this.currentType = 'news';
                  break;
          case 2: this.currentType = 'sell';
                  break;
        }
        //将两个属性设置为相同的属性
        this.$refs.tabControl1.currentIndex = index;
        this.$refs.tabControl2.currentIndex = index;

      },
      //置顶按钮的显示与隐藏
      cScroll(position) {
        this.isShowBackTop = (-position.y) > 700;
        //决定tabcontrol是否吸顶 
        this.isTabControl = (-position.y) > this.tabOffsetTop
      },
      getHomeGoods(kind) {
        //获取要获取数据的页码
        const page = this.goods[kind].page + 1;
        const typepage = kind + "_" + page;
         getHomeGoods(typepage).then(res => {
           //将数据放到数组中
           this.goods[kind].list.push(...res);
           this.goods[kind].page += 1;
           //刷新  判断数据是否到底
           this.$refs.scroll.finishPullUp();
        })
      },
      //上拉加载更多
      loadMore() {
        //监听图片的上拉  调用方法增加数据
        this.getHomeGoods(this.currentType);
        //刷新scroll  
        this.$refs.scroll.refresh();
      }

      //处理请求的多个数据   并将数据放到对应的数组中

      
    }
  }
</script>

<style scoped>
  #home{
    /* padding-top: 44px; */
    /*设置视口 */
    height: 100vh;
    position: relative;
  }
/*首页导航栏样式 */
  .nav-style{
    background-color: #ff8198;
    color:white;
    /* position: fixed;
    z-index: 10;
    left: 0;
    right: 0;
    top: 0; */
  }
  .nav-right, .nav-left{
    margin-top: 11px;
  }
  li{
    height: 30px;
  }
  .tabcon{
    /* 置顶停留  */
    position: relative;
    z-index: 10;
  }
  /*设置滚动部分的属性 */
  .content{
    /*使用定位获取better-scroll的高度 */
    position: absolute;
    top: 44px;
    left: 0;
    right: 0;
    bottom: 45px;
    overflow: hidden;
  }
</style>