<template>
  <div class="trans">
    <div class="transimg">
      <div class="img1"></div>
      <div class="img2"></div>
      <div class="img3"></div>
      <div class="img4"></div>
      <div class="img5"></div>
      <div class="img6"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'TransThree'
  }
</script>

<style scoped>
  *{
    box-sizing: border-box;
  }
  .trans{
    display: flex;
    align-items: center;
    justify-content: center;
    /* height: 100vh; */
  }
  .transimg{
    width: 200px;
    height: 200px;
    transform-style: preserve-3d;
    animation: animas 5s ease-in-out infinite;
  }
  .transimg > div{
    width: 200px;
    height:200px;
    background-size: cover;
    background-position: center center;
    opacity: 0.8;
    position:absolute;
    box-shadow: inset 0 0 4px 2px rgba(106, 106, 106, 0.4);
  }
  /*前 */
  .transimg .img1{
    background-image: url("../../../assets/img/111.png");
    transform: translateZ(100px);
  }
  /*右 */
  .transimg .img2{
    background-image: url("../../../assets/img/222.png");
    transform:rotateY(90deg) translateZ(100px);
  }
  /*左 */
  .transimg .img3{
    background-image: url("../../../assets/img/333.png");
    transform: rotateY(-90deg) translateZ(100px);
  }
  /*下 */
  .transimg .img4{
    background-image: url("../../../assets/img/111.png");
    transform: rotateX(90deg) translateZ(100px);
  }
  /*上 */
  .transimg .img5{
    background-image: url("../../../assets/img/222.png");
    transform: rotateX(-90deg) translateZ(100px);
  }
  /*后*/
  .transimg .img6{
    background-image: url("../../../assets/img/333.png");
    transform: rotateX(-180deg) translateZ(100px);
  }
  @keyframes animas{
    0% {
      transform: rotateX(0) rotateY(0);
    }
    20% {
      transform: rotateY(-90deg);/*右 */
    }
    40% {
      transform: rotateX(-90deg);/*上 */
    }
    60% {
      transform: rotateY(90deg);/*左 */
    }
    80% {
      transform:rotateX(90deg); /*下 */
    }
    100% {
      transform:rotateX(0) rotateY(0);    
    }
  }

</style>